// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'mint-ui/lib/style.css'
// import './mockjs/mock'

import Mint from 'mint-ui'
Vue.use(Mint);

import Axios from 'axios'
Vue.prototype.$axios = Axios;

import animated from 'animate.css'
Vue.use(animated);

import base from "./js/base.js";
Vue.use(base);

import sanGong from "./js/sanGong.js";
Vue.use(sanGong);

import sangong_ from "./components/sanGong";
Vue.prototype.SANGONG = sangong_;

import global_ from "./components/global";
Vue.prototype.GLOBAL = global_;

// import vuex from 'vuex'
// Vue.use(vuex);

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>',
    beforeDestroy: function () {
    },
    created: function () {
        if(!this.isMobile()){
            if(!this.GLOBAL.appInfo.error_str){
                this.GLOBAL.appInfo.error_str = '对不起，请通过手机访问！';
            }
            this.$messagebox.alert(this.GLOBAL.appInfo.error_str);
            this.$router.push('/error');
        }
        // 判断本地缓存是否存在
        if(localStorage.getItem('bc_appInfo') != null){
            // 将缓存数据赋值到全局属性中
            this.setGlobalAttribute(this.GLOBAL.appInfo, JSON.parse(localStorage.getItem('bc_appInfo')));
        }
        if(localStorage.getItem('bc_userInfo') != null){
            this.setGlobalAttribute(this.GLOBAL.userInfo, JSON.parse(localStorage.getItem('bc_userInfo')));
        }
        if(localStorage.getItem('bc_friendInfo') != null){
            this.setGlobalAttribute(this.GLOBAL.friendInfo, JSON.parse(localStorage.getItem('bc_friendInfo')));
        }
    },
    watch:{
    }
})


console.log('MAIN');


